@import url(https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::after,*::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    width: 100%;
}
body {
    font-family: 'Sen';
    background-color: #F3F1D0;
    width: 100%;
}
.homepage-section {
    min-height:100vh;
    background-color: #F3F1D0;
    margin-left: 0 auto;
    margin-right: 0 auto;
    margin-top: 15px;
    width: 100%;
    padding: 15px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 16px;
    gap: 16px;
    place-items: center;
}
.homepage-section-search {
    height:60vh;
    margin-left: 0 auto;
    margin-right: 0 auto;
    background-color: #F3F1D0;
    margin-top: 40px;
    width: 100%;
    padding: 15px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 16px;
    gap: 16px;
    place-items: center;
}
.exist {
    font-size: 30px;
    color: red;
    padding: 7px 15px;
    text-shadow: 1px 1px 2px blue;
    margin-top: 30px;
    margin-left: 30px;
    border-radius: 5px;
    width: 373px;
    background-color: chartreuse;
    -webkit-animation: ex infinite 2s ease ;
            animation: ex infinite 2s ease ;
}
@-webkit-keyframes ex {
    from {background-color: red;color: black;}
    to {background-color: yellow;}
  }
@keyframes ex {
    from {background-color: red;color: black;}
    to {background-color: yellow;}
  }
.homepage-section .more,
.card .more {
    float: right;
    font-size: 16px;
    position: absolute;
    left: 79%;
    top: 90%;
