:root {
    --dark : #16171C;
    --light : #F3F1D0;
}

nav {
    background-color: #16171C;
    background-color: var(--dark);
    left: 0;
    top: 0;
    color: #F3F1D0;
    color: var(--light);
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    align-items: center;
    z-index: 1;
    width: 100%;
    height: 8vh;
}
nav ul {
    height: 7vh;
    display: flex;
    list-style: none;
    background-color: #16171C;
    background-color: var(--dark);
    width: 100%;
    align-items: center;
    margin-top: 11px;
    opacity: 1;
    padding-left: 5px;
}
nav ul li {
    width: 20%;
    display: flex;
    align-items: center;
}
nav .countries {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    left: 91%;
}
.nav ul li a {
    display: block;
    text-decoration: none;
    color:#F3F1D0 ;
    text-transform: uppercase;
    top: +2px;
}
.nav ul li a:active {
    top: +1px;
    color: #2196f3;
}
input::-webkit-input-placeholder {
    border: none;
    outline: none;
    padding: 7px 7px;
    height: 40px;
    caret-color: #F3F1D0;
    caret-color: var(--light);
    border-radius: 5px;
    letter-spacing: 1px;
    font-size: 18px;
    border: 1px solid #16171C;
    border: 1px solid var(--dark);
    color: #F3F1D0;
    color: var(--light);
    box-shadow: rgba(80, 75, 75, 0.4) 0px 2px 4px, rgba(97, 95, 95, 0.3) 0px 7px 13px -3px, rgba(133, 131, 131, 0.2) 0px -3px 0px inset;
    background-color: #16171C;
    background-color: var(--dark);
    margin-top: 25px;
    margin-left: -50px;
    width: 320px;
}
input:-ms-input-placeholder {
    border: none;
    outline: none;
    padding: 7px 7px;
    height: 40px;
    caret-color: #F3F1D0;
    caret-color: var(--light);
    border-radius: 5px;
    letter-spacing: 1px;
    font-size: 18px;
    border: 1px solid #16171C;
    border: 1px solid var(--dark);
    color: #F3F1D0;
    color: var(--light);
    box-shadow: rgba(80, 75, 75, 0.4) 0px 2px 4px, rgba(97, 95, 95, 0.3) 0px 7px 13px -3px, rgba(133, 131, 131, 0.2) 0px -3px 0px inset;
    background-color: #16171C;
    background-color: var(--dark);
    margin-top: 25px;
    margin-left: -50px;
    width: 320px;
}
input[type='search'],
input::placeholder {
    border: none;
    outline: none;
    padding: 7px 7px;
    height: 40px;
    caret-color: #F3F1D0;
    caret-color: var(--light);
    border-radius: 5px;
    letter-spacing: 1px;
    font-size: 18px;
    border: 1px solid #16171C;
    border: 1px solid var(--dark);
    color: #F3F1D0;
    color: var(--light);
    box-shadow: rgba(80, 75, 75, 0.4) 0px 2px 4px, rgba(97, 95, 95, 0.3) 0px 7px 13px -3px, rgba(133, 131, 131, 0.2) 0px -3px 0px inset;
    background-color: #16171C;
    background-color: var(--dark);
    margin-top: 25px;
    margin-left: -50px;
    width: 320px;
}
.giHam {
    display: none;
}

@media only screen and (max-width: 768px) {
    nav {
        display: block;
        padding-left: 0;
        padding-top: 6px;
    }
    input[type='search'] {
        width: 250px;
        margin-left: -93px;
    }
    .search-h4 {
        font-size: 25px;
        margin: 55px 0 0 15px;
    }
    nav ul li {
        width: 23%;
    }
    nav .countries li a {
        padding: 0;
        cursor: pointer;
        margin-right: -52px;
        margin-left: 54px;
    }
    nav ul li a {
        display: block;
        text-decoration: none;
        color:#16171C;
        color:var(--dark);
        margin: 3px 5px;
        margin-right: 10px;
        font-weight: bold;
        cursor: pointer;
        font-size: 15px;
        

    }
    nav ul li a:hover ,
    nav .countries li a:hover {
        color: rgb(51, 176, 214);
    }
    .category  {
        margin-left: 15px;
    }
    .category-search  {
        margin-left: 45px;
    }
    .giHam {
        display: block;
        cursor: pointer;
        font-size: 40px;
      }
      .giHam:active {
        -webkit-transform: rotate(320deg);
                transform: rotate(320deg);
      }
      .homepage-section,
      .homepage-section-search {
          display: block;
      }
    }

  .nav.active {
    top: 8%;
    background-color: #16171C;
    background-color: var(--dark);
  }
  .nav.active ul   {
    display: none;
  }
  .nav.active ul a {
      color: white;
      text-decoration: none;
  }
  @media only screen and (min-width: 769px) and (max-width:1550px) {
  nav ul {
    margin-top: 5px;
}
.nav.active ul {
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 16px;
    margin-left: -160px;
}
.nav.active {
    top: 0%;
  }
  
}
