.category-wrapper {
    margin: 20px;
    border-radius: 5px;
    padding: 15px;
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.category-wrapper > * {
    display: grid;
    grid-template-columns: repeat(5,1fr);
    margin-top: 5px;
}
.category-wrapper ul li {
    margin-left: 40px;
}
.category-wrapper img {
    margin-left: 27px;
    width: 20px;
    cursor: pointer;
    height: 20px;
}
.categories-card {
    max-width: 255px;
    min-height: 255px;
    height: 255px;
    padding: 18px 20px 25px 20px; 
    background-color: #fff;
    position: relative;
    border-radius: 5px;
    margin-bottom: 20px;
    margin-right: auto;
    margin-left: auto;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;

}
.categories-h2 {
    margin-left: 40px;
}
.categories-card p,
.categories-card a {
    word-break: break-all;
    max-width: 25ch;
    padding: 2px;
}
.categories-card a {
    color: rgb(163, 26, 95);
    margin-top: 10px;
    position: absolute;
    top: 75%;
    margin-bottom: 5px;
    font-size: 1rem;
}
.categories-card p {
    opacity: 0.7;
}

.categories-card .more {
position: absolute;
left: 67%;
top: 90%;
}
.arrows {
   width: 18px;
   height: 18px;
   border-color: 1px solid #000;
   position: absolute;
   top: 50%;
   cursor: pointer;
}
.prev {
    border-bottom: 4px solid;
    border-left: 4px solid;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    left: 10px;
}
 
.next {
    border-bottom: 4px solid;
    border-left: 4px solid;
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
    right: 10px;
 
}
   
h3 {
    text-transform: capitalize;
    font-weight: 900;
    margin: 3px 0;
    letter-spacing: 1px;
}
ul {
    display: flex;
}
li {
    font-size: 18px;
}
a {
    color: rgb(163, 26, 95);
    font-size: 20px;
    font-weight: 800;
    letter-spacing: 1px;
    display: block;
    margin-bottom: 8px;
}
.show {
   display: none;
}
.show.active {
   display: block;
   height: 270px;
}
